  height: 50px;
  width: 50px;
  margin-left: 20px;
}

.legend_text {
  margin-left: 10px;
  text-align: left;
}

.legend_square1 {
	height: 50px;
  	width: 50px;
	background-color: #892536;
	margin-left: 20px;
}

.legend_square2 {
	height: 50px;
  	width: 6px;
	background-color: #398bfc;
	margin-left: 20px;
}

.legend_square3 {
	height: 50px;
  	width: 50px;
	background-color: #362251;
	margin-left: 20px;
}

