 .App {
  text-align: center;
}

.App-logo {
  /*animation: App-logo-spin infinite 20s linear;*/
  height: 20vmin;
  pointer-events: none;
} 

 .App-header {
  /*background-color: #282c34;*/
  /*background-image: url(./assets/images/deforestation.jpg);*/
  background-image: url(/strom/static/media/head-bg3.7b535aa8.jpg);
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
