} 

 .App-link {
  color: #61dafb;
}

 .App-footer {
  background-color: #282c34;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /*font-size: calc(10px + 2vmin);*/
  color: white;
  padding-right: 5%;
  padding-left: 5%;
} 

.Footer-logo {
  /*animation: App-logo-spin infinite 20s linear;*/
