  pointer-events: none;
} 

.Footer-div {
  padding-bottom: 20px;
}

.description {
  /*background-color: #000000;*/
  /*text-decoration-color: white;*/
  /*filter: alpha(opacity=60); /* For IE8 and earlier */
  /*opacity: 0.5;*/
  background-color: rgba(0,0,0,0.6);
  margin: 5%;
  padding-right: 5%;
  padding-left: 5%;
  padding-top: 3%;
  padding-bottom: 3%;
}

.btn-dark {
    border-radius: 0;
    color: #fff;
    background-color: rgba(0,0,0,0.4);
    margin-bottom: 20px;
    margin-top: 20px;
}

.btn-dark:hover,
.btn-dark:focus,
.btn-dark:active {
    color: #fff;
    background-color: rgba(0,0,0,0.7);
}

.call-to-action .btn {
    margin: 10px;
}

.nav-item > .active {
    border-radius: 0;
    color: #fff;
    background-color: rgba(0,0,0,0.4);
    margin-bottom: 50px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
} 

